<template>
  <div class="commonDialog">
    <vxe-modal v-model="value" :key='commonDialog' title='填写物流信息' @hide="$emit('input', false)" width="400" :esc-closable='true' destroy-on-close height="250" min-width="600" min-height="100" resize remember storage transfer>
      <el-form :model="params" ref='logistics'>
        <el-form-item label="物流公司">
          <el-select v-model="params.express_id" @focus="getLogistics" filterable :loading='isLoading'>
            <el-option v-for="(val, key) in  logistics" :label="val.e_name" :value='val.id' :key='key'></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="物流票号" :rules="{ required:true,message: '请输入物流票号' }">
          <el-input v-model="params.invoice_no"></el-input>
        </el-form-item>
      </el-form>
      <div class="dialog-footer">
        <el-button type="primary" @click="submit">确定</el-button>
        <el-button @click="$emit('input', false)">取 消</el-button>
      </div>

    </vxe-modal>
  </div>
</template>

<script>
export default {
  data() {
    return {
      logistics: [],
      isLoading: false,
      params: {
        invoice_no: "",
        express_id: ""
      }
    };
  },
  watch: {
    value(newVal) {
      if (newVal === true) {
        this.params = {
          invoice_no: "",
          express_id: ""
        };
      }
    }
  },
  props: ["refund_id", "value"],
  methods: {
    submit() {
      this.$emit("close");
      this.params.refund_id = this.refund_id;
      this.$axMethod.serviceShipReturn(this.params).then(({ data }) => {
        if (data.code == 200) {
          this.$message.success(data.msg);
          this.$emit("success", data);
        }
      });
    },
    getLogistics() {
      this.isLoading = true;
      this.$axMethod
        .getShopLogisticsList()
        .then(({ data }) => {
          if (data.code == 200) {
            console.log(data);
            this.logistics = data.data;
          }
          this.isLoading = false;
        })
        .catch(() => {
          this.isLoading = false;
        });
    }
  }
};
</script>

