<script>
import logistics from "./config/logistics";
export default {
  name: "purchasePurchaserefundReturnRequestSee",
  data() {
    return {
      isShowLogistics: false,
      params: {
        extend_order: {}
      }
    };
  },
  components: {
    logistics
  },
  methods: {
    logisticsChange() {
      this.isShowLogistics = false;
      this.getData();
    },
    goBack() {
      this.$router.push({ path: "/purchase/returnRequest" });
    },
    getData() {
      if (this.$route.query.id) {
        this.$axMethod
          .serviceGetReturnInfo({ refund_id: this.$route.query.id })
          .then(({ data }) => {
            if (data.code == 200) {
              this.params = data.data;
              console.log(this.params);
              if (this.params.pic_info && this.params.pic_info.buyer) {
                // this.upload_pic = this.params.pic_info.buyer;
                this.$refs.cupload._initList(this.params.pic_info.buyer);
              }
            }
          });
      }
    }
  },
  created() {
    this.getData();
  }
};
</script>

